.button {
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    background: #FFFFFF;
    border: 1px solid #F3F3F3;
    box-shadow: 0 4px 4px rgba(133, 133, 133, 0.25);
    border-radius: 17px;
    padding: 10px 14px;
    color: #B4B4B4;
    margin-right: 24px;
}

.videoPickerWrapper{
    display: flex;
    align-items: center;

    &:hover{
        .deleteButtonClassName{
            opacity: 1;
        }
    }
}
.deleteButtonClassName{
    opacity: 0;
    transition: opacity 0.3s;
}

.videoWrapper{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 72px;
    height: 72px;
    margin-right: 30px;
}

.video {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: block;
    margin-bottom: 10px;
    max-width: 100%;
    max-height: 100%;
}

.video_0{
    z-index: 1;
}

.video_1{
    transform: translate(-50%, -50%) rotate(10deg)
}

.video_2{
    transform: translate(-50%, -50%) rotate(-10deg)
}

.video_3{
    transform: translate(-50%, -50%) rotate(20deg)
}

.video_4{
    transform: translate(-50%, -50%) rotate(-20deg)
}